        .notification-messages {
            position: fixed;
            right: 16px;
            top: 64px;
        }
    }
    &__info {
        background: var(--fill-disabled);
        margin-top: 5rem;
        font-size: 1.4rem;
        color: var(--text-general);
        display: grid;
        grid-template-columns: 0.85fr 1fr 1fr;
        height: 56px;
        border-radius: $BORDER_RADIUS;

        &-description,
        &-copy {
            display: flex;
            align-items: center;
        }
        &-description {
            justify-content: flex-start;
            padding-left: 3.2rem;
        }
        &-copy {
            justify-content: center;
            font-size: 1.6rem;
            font-weight: bold;
            color: var(--text-prominent);
        }
        &-display {
            padding-right: 0.8rem;
        }
        &-label {
            padding-right: 0.4rem;
        }
        @include mobile {
            display: flex;
            height: 100%;
            padding: 1.6rem;
            flex-direction: column;
            align-items: flex-start;

            &-copy {
                margin: 0 0 0.8rem;
                font-size: 1.4rem;
            }
            &-description {
                margin: 0.8rem 0 1.6rem;
            }
        }
    }
    &__welcome-message {
        margin-bottom: 2.4rem;

        &--heading {
            @include typeface(--title-center-bold-black, none);
            color: var(--text-general);
        }
    }
    &__missing-real {
        align-items: center;
        background-color: var(--general-section-1);
        display: flex;
        justify-content: space-between;
        margin: 2.4em 0 0;
        margin-inline: auto;
        padding: 1.6rem;
        max-width: 96rem;
        &-wrapper {
            max-width: 90%;
            margin-right: 1rem;

            &--heading {
                @include desktop {
                    white-space: nowrap;
                }
                @include typeface(--title-left-bold-black, none);
                @include mobile {
                    font-size: 1.2rem;
                }
            }
        }
        &--button {
            justify-self: flex-end;
        }
        @include mobile {
            flex-direction: column;
            padding: 0.8rem;
            width: calc(100vw - 3.2rem);

            &--heading {
                margin-bottom: 0.8rem;
                margin-right: 0;
            }
            &--button {
                width: 27rem;
            }
        }
    }

    &__social-banner {
        width: 100%;
        background-color: var(--general-section-1);
        opacity: 95%;
        top: 0;
        position: absolute;
        z-index: 1;

        &--close-icon {
            position: absolute;
            top: 1.6rem;
            right: 1.6rem;
        }

        &--wrapper {
            display: flex;
            flex-direction: row;
            gap: 2.4rem;
            align-items: center;
            justify-content: center;
            padding: 0.8rem 0;
            margin-inline: 2.4rem;

            &__image {
                width: 16rem;
                height: 12rem;
            }

            &__button {
                float: right;
                padding: 0.8rem;
            }
        }
    }

    &__download-container {
        border-top: 2px solid var(--general-section-1);
        padding-top: 2rem;
        display: flex;
        align-items: center;
        justify-content: space-evenly;

        @include mobile {
            margin-bottom: 1.6rem;
        }

        &--is-mt5 {
            display: block;
            border-top: unset;
            padding-top: unset;
        }

        &-mobile-hint {
            margin: 2.4rem 0;
            display: block;
        }

        &-heading {
            @include typeface(--title-center-bold-black, none);
            color: var(--text-general);
            line-height: 1.5;
            margin-bottom: 2.4rem;
            @include desktop {
                margin-left: 0;
                text-align: center;
            }
        }

        &-links {
            $parent: &;

            &--is-mt5 {
                display: flex;
                align-items: baseline;
                justify-content: center;

                #{$parent}--desktop {
                    margin-right: 6.4rem;
                }

                @include mobile {
                    display: block;
                }
            }

            &--desktop {
                margin-bottom: 6.4rem;
            }

            &-icons {
                text-align: center;
                margin-bottom: 1.6rem;

                svg {
                    margin: 0 0.4rem;
                    display: inline-block;
                }
            }

            &-download-buttons {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-wrap: wrap;

                &--column {
                    flex-direction: column;
                }

                & > a {
                    display: inline-block;
                    margin: 0.4rem;
                }
            }
        }

        &-dxtrade-button {
            background: $color-black-7;
            border-radius: $BORDER_RADIUS;
            text-decoration: none;
            display: grid;
            grid-template-columns: 32px 1fr;
            grid-gap: 0.8rem;
            width: 12.7rem;
            align-items: center;
            height: 4rem;
            border: 1px solid #a6a6a6;
            margin: 0 auto;

            &-text {
                display: flex;
                flex-direction: column;
                padding: 0.4rem 0rem 0.4rem 0.2rem;
            }
            &-icon {
                margin-left: 0.6rem;
            }
        }

        &-qrcode {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            border: 1px solid $color-grey-4;
            border-radius: $BORDER_RADIUS;
            padding: 2.4rem 2.4rem 0.8rem;
            margin-left: 10rem;

            &__hint {
                @include typeface(--small-center-normal-black, none);
                color: $color-black-1;
                margin-top: 0.8rem;
                width: 160px;
            }
        }
    }
    &__maintenance {
        @include typeface(--small-center-normal-black, none);
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 2.4rem auto;

        &-icon {
            margin-right: 0.8rem;
            margin-top: 0.2rem;

            @include mobile {
                margin-top: -0.2rem;
            }
        }
        &-text {
            text-align: left;
            line-height: 1.67;
        }
    }
    &__real-accounts-wrapper {
        display: flex;
        flex-direction: column;
    }
    .notification-messages {
        top: calc(-4.8rem + 16px);
    }
    @include mobile {
        margin-top: 2.4rem;
        padding: 0 1.6rem;
    }
}

.cfd-attribute-describer {
    .counter {
        color: var(--brand-red-coral);
        vertical-align: top;
    }
    @include mobile {
        font-size: 1.2rem;
    }
}

.cfd-compare-accounts {
    display: flex;
    flex-flow: column nowrap;
    flex-grow: 1;
    background: inherit;
    height: 100%;
    position: relative;

    &__table {
        @include mobile {
            overflow-x: auto;
        }
    }

    &__table-row {
        grid-template-columns: var(--cfd-compare-accounts-template-columns);
        box-shadow: inset 0 -1px 0 0 var(--general-section-1);
    }
    &__table-row-eu {
        grid-template-columns: 1.5fr 2fr 2fr;
        box-shadow: inset 0 -1px 0 0 var(--general-section-1);
    }
    &__star {
        color: var(--brand-red-coral);
    }
    &__bullet {
        flex: none;

        &--circle {
            background-color: var(--text-general);
            border-radius: 100%;
            margin-right: 1.2rem;
            margin-left: 0.2rem;
            margin-top: 0.8rem;
            width: 0.4rem;
            height: 0.4rem;
        }
        &--star {
            margin-right: 1rem;
        }
        &-wrapper {
            display: flex;
            margin-bottom: 0.8rem;
            line-height: 1.5;
        }
    }

    &__footnote {
        &-title {
            padding-bottom: 0.8rem;
        }
    }

    .dc-table {
        padding: 0 30px;

        &__header {
            border-bottom: none;
            height: 50px;
            position: sticky;
            top: 0;
            background: var(--general-main-2);
            z-index: 10;
        }
        &__cell,
        &__head {
            padding: 0.8rem;

            &--fixed {
                padding-left: 1.6rem;

                @include mobile {
                    background-color: var(--general-section-2);
                }
            }
        }
        &__cell {
            border-bottom: none;
            align-items: normal;
            padding: 1rem 0.8rem;
        }
        &--scrollbar {
            display: block; // Safari needs this to work properly!
        }
        @include desktop {
            &__header {
                background-color: var(--general-main-2);
            }
        }
    }
    &-modal {
        &__wrapper {
            display: flex;
            justify-content: center;
            margin-top: 2.4rem;
        }
    }
    @include mobile {
        background: var(--general-main-1);
        height: 100%;
        font-size: 1.2rem;

        .dc-table {
            padding: 0;
            height: inherit;

            &__cell {
                font-size: 1.2rem;
            }
            &__row {
                padding: 0;
            }
            &__head {
                font-size: 1.2rem;
                border-bottom: 2px solid var(--border-disabled);
                background-color: var(--general-main-1);
                margin-top: -1px;
            }
            &__header,
            &__row {
                width: 150vw;
                grid-template-columns: 1fr 1fr 1fr 1fr;
            }
        }
    }
}

.cfd-compare-account--hint {
    color: var(--text-less-prominent);
    font-size: 12px;
    line-height: 1.5em;
    width: 100%;
    padding: 1.6rem 2.4rem 0.8rem 2.4rem;

    @include mobile {
        padding: 1.6rem;
        border-top: 2px solid var(--border-disabled);
    }
}

.cfd-dashboard__accounts-error {
    background-color: var(--status-warning-transparent);
    margin: 1.2rem 0;

    &-message {
        padding: 0.8rem 2.4rem;
    }
}

.cfd-real-accounts-display,
.cfd-demo-accounts-display {
    display: grid;
    grid-auto-flow: column;
    grid-gap: 2.4rem;
    justify-content: center;
    padding-top: 2.4em;
    height: fit-content;

    @include mobile {
        display: flex;
        gap: 0;
        overflow-x: hidden;
        flex-direction: column;
        height: unset;
    }
}

.cfd-jurisdiction-card--synthetic,
.cfd-jurisdiction-card--financial,
.cfd-jurisdiction-card--all {
    border: solid 2px var(--border-normal);
    border-radius: $BORDER_RADIUS * 4;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    height: 100%;
    width: 100%;
    cursor: pointer;
    transition: all 0.1s ease-in;

    &:not(.selected-card):not(.cfd-card-disabled):not(.cfd-card-disabled-flat):hover {
        box-shadow: 0 2px 8px 0 var(--shadow-menu);
    }

    &--disabled {
        border: solid 1px var(--border-disabled);
    }

    &__wrapper {
        margin: 4rem 6rem 2rem;
        display: flex;
        justify-content: center;
        gap: 2rem;
        @include mobile {
            margin: 0;
            padding: 4rem 4rem;
            gap: 4rem;
            flex-direction: column;
            align-items: center;
            overflow-y: scroll;
            height: 60%;
        }
    }

    &--selected {
        border: solid 2px $color-green;
        box-shadow: 0 1.2rem 1.8rem 0.8rem var(--shadow-menu);
    }

    &__card {
        &-content {
            &-container {
                display: flex;
                flex-direction: column;
                height: 100%;
            }
            &-over-header {
                background: var(--text-info-blue-background);
                height: 4rem;
                width: 100%;
                padding: 0.5rem;
                border-radius: 1.4rem 1.4rem 0 0;
                &-blank {
                    height: 4rem;
                    border-radius: 1.4rem;
                    @include mobile {
                        height: 2rem;
                    }
                }
            }
            &-footer {
                width: 100%;
                &-text {
                    background: var(--brand-secondary);
                    height: 4rem;
                    padding: 1rem;
                    border-radius: 0 0 1.4rem 1.4rem;
                }
            }
        }
        &-flipped-container {
            gap: 2rem;
            padding: 4rem 2rem;
        }
        &-section-container {
            display: flex;
            flex-direction: column;
            gap: 1.2rem;
            height: 100%;
            margin: 0 2rem;
        }
    }

    &__h2-header {
        min-height: 5rem;
        margin: 1rem 2rem;
        @include mobile {
            margin-bottom: 2rem;
            min-height: auto;
        }
    }

    &__footer-wrapper {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        @include mobile {
            min-height: auto;
            background-color: var(--general-main-1);
            width: 100%;
            height: 100%;
            position: sticky;
            bottom: 0;
            z-index: 12;
        }
    }

    &__footnotes-container {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin: 1rem;
        @include desktop {
            min-height: 6rem;
        }
        @include mobile {
            gap: 0;
            margin: 0;
        }
    }

    &__footnote {
        display: flex;
        justify-content: center;
        @include mobile {
            margin: unset;
            padding: 1.6rem 0.8rem;
            box-shadow: 0px -4px 4px rgba(0, 0, 0, 0.08);
        }
        &--pending {
            color: $color-yellow;
        }
    }

    &__jurisdiction-checkbox {
        display: flex;
        justify-content: center;
        @include mobile {
            padding: 0.8rem;
        }
    }

    &__over-header {
        background-color: #dfeaff;
        top: -2rem;
        left: 1.4rem;
        z-index: 10;
        width: 90%;
        height: 4rem;
        position: absolute;
        border-radius: $BORDER_RADIUS * 2;
        margin-bottom: 1rem;
        border: 1px solid var(--text-info-blue);
        & > p {
            font-size: 16px;
            text-align: center;
        }
    }
}

.cfd-card {
    &-perspective {
        -webkit-perspective: 100rem;
        perspective: 100rem;
        @include mobile {
            width: 100%;
        }
    }
    &-container {
        -webkit-transform-style: preserve-3d;
        transform-style: preserve-3d;
        -webkit-transition: all 0.25s ease-in;
        transition: all 0.25s ease-in;
        width: 29rem;
        &.synthetic,
        &.all {
            min-height: 48rem;
        }
        &.financial {
            min-height: 54rem;
        }
        @include mobile {
            height: 48rem;
            width: 100%;
        }
    }
    &-flipped {
        -webkit-transform: rotateY(180deg);
        transform: rotateY(180deg);
    }
    &-front,
    &-back {
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
        -webkit-transform-style: preserve-3d;
        transform-style: preserve-3d;
        position: absolute;
        top: 0;
        left: 0;
    }
    &-front {
        z-index: 2;
        -webkit-transform: rotateY(0deg);
        transform: rotateY(0deg);
    }
    &-back {
        -webkit-transform: rotateY(-180deg);
        transform: rotateY(-180deg);
    }
    &-section {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 0.8rem;
        &-divider {
            width: 100%;
            border-bottom: 2px solid var(--border-divider);
        }
        &-title-indicator {
            padding: 0.6rem 1.2rem;
            border-radius: $BORDER_RADIUS;
            &-icon-container {
                display: flex;
                gap: 0.5rem;
            }
            &__red-darker {
                background: $color-red-8;
            }
            &__red-dark {
                background: $color-red-9;
            }
            &__red-light {
                background: $color-red-10;
            }
            &__yellow-dark {
                background: $color-yellow-1;
            }
            &__yellow-light {
                background: $color-yellow-2;
            }
            &__violet-dark {
                background: $color-violet;
            }
            &__brown-dark {
                background: $color-brown;
            }
        }
        &-description-height {
            @include desktop {
                &-assets {
                    min-height: 3.4rem;
                }
                &-verifications,
                &-regulator {
                    min-height: 5rem;
                }
            }
        }
    }
    &-back-section {
        &-items-container {
            display: flex;
            flex-direction: column;
            gap: 1.2rem;
        }
        &-items-sub-container {
            display: flex;
            align-items: flex-start;
            gap: 1rem;
        }
        &-back-button {
            -webkit-transition: transform 0.25s linear;
            transition: transform 0.25s linear;
        }
        &-text-icon-aligned {
            margin-top: -0.3rem;
        }
    }
    &-clickable-description-link {
        color: var(--text-red);
        &:hover {
            text-decoration: underline;
        }
    }
    &-title-container {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &-disabled {
        &-flat {
            border: solid 1px var(--border-normal);
        }
    }
}

.cfd-modal--custom-exit {
    display: none;
}

.cfd-real-accounts-display {
    transition: margin-bottom 0.3s ease-in-out;
    margin-bottom: var(--cfd-real-acc-margin-bottom);

    .dc-carousel__card,
    .dc-carousel__wrapper {
        padding: 0;
    }

    &--has-trade-servers {
        .cfd-account-card__wrapper {
            @include desktop {
                grid-template-rows: 1fr 4rem;
            }
        }
    }
}

.cfd-account-card {
    border: solid 1px var(--border-normal);
    border-radius: 4px;
    display: flex;
    flex-flow: column nowrap;
    min-height: 21rem;
    width: 30.4em;
    position: relative;
    justify-content: space-between;
    @include desktop {
        height: 100%;
    }

    &__balance {
        margin-bottom: 0.8rem;
        padding: 0 0.8rem;
        &--region {
            border: 1px solid #2a3052;
            border-radius: 1rem;
            background-color: #2a3052;
            padding: 0.2rem 0.8rem;
            margin-left: 0.8rem;
        }
        &--value {
            @include typeface(--title-left-bold-black);
            color: var(--text-profit-success);
        }
    }

    &--login-id {
        padding: 0 0.8rem;
    }

    &--login-id-demo {
        padding: 0.8rem 0.8rem 0;
    }

    &__item {
        width: 90%;
        background-color: var(--general-section-1);
        border-radius: 8px;
        margin-bottom: 0.8rem;

        &--banner {
            position: relative;
            transform: rotate(45deg);
            border-bottom: 1.8rem solid #85acb0;
            border-left: 1.8rem solid transparent;
            border-right: 1.8rem solid transparent;
            height: 0;
            width: 7rem;
            @include desktop {
                left: 21.8rem;
            }
            @include mobile {
                margin-left: 1.8rem;
            }
            top: 1rem;
            color: #fff;
            font-weight: bold;
            line-height: 18px;
            white-space: nowrap;
            text-align: center;

            &--demo {
                border-bottom-color: var(--brand-secondary);
            }
            &--server {
                border-bottom-color: $color-blue-2;
            }
            @include mobile {
                left: calc(100vw - 14.2rem);
            }
        }
    }

    &__wrapper {
        display: grid;
        grid-template-rows: 1fr;
        justify-content: center;
        height: fit-content;

        @include mobile {
            grid-template-rows: 1fr;

            &:not(:last-child) {
                margin-bottom: 2.4rem;
            }
        }
    }

    &__logged-out {
        min-height: 15rem;
    }

    &__add-server {
        font-size: 1.4rem;
        cursor: pointer;
        display: grid;
        grid-auto-flow: column;
        grid-gap: 0.4rem;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 4rem;
        color: var(--text-prominent);

        @include mobile {
            height: 4rem;
            align-content: start;
        }

        &--disabled {
            cursor: not-allowed;
            color: var(--text-disabled);

            .cfd-account-card__add-server--icon {
                background-color: var(--border-disabled);
                color: var(--text-disabled);
            }
        }
        &-exit,
        &-enter-done {
            transition: all 0.3s ease-in-out;
            opacity: 1;
        }
        &-enter,
        &-exit-done {
            transition: all 0.3s ease-in-out;
            opacity: 0;
        }
        &--icon {
            color: var(--status-colored-background);
            background-color: var(--brand-red-coral);
            border-radius: 100%;
            width: 2.4rem;
            height: 2.4rem;
            font-size: 1.8rem;
            display: block;
            text-align: center;
            padding: 0.2rem;
        }
    }

    &__server {
        font-size: 1.4rem;
        text-align: left;
        width: 27.2rem;
        &--value {
            font-weight: bold;
        }
    }

    &:hover {
        box-shadow: 0 2px 8px 0 var(--shadow-menu);
    }
    &--heading {
        @include typeface(--title-left-bold-black, none);
        color: var(--text-general);
        line-height: 1.5;
    }
    &--paragraph {
        @include typeface(--paragraph-left-normal-black, none);
        color: var(--text-general);
        line-height: 1.45;
    }
    &__banner {
        position: absolute;
        transform: rotate(45deg);
        border-bottom: 2.3rem solid var(--brand-red-coral);
        border-left: 2.3rem solid transparent;
        border-right: 2.3rem solid transparent;
        height: 0;
        width: 11.7rem;
        left: 21.1rem;
        top: 2.1rem;
        color: var(--text-colored-background);
        font-weight: bold;
        line-height: 24px;
        white-space: nowrap;
        text-align: center;

        &--demo {
            border-bottom-color: var(--brand-secondary);
        }
        &--server {
            // TODO: Add a CSS variable
            border-bottom-color: $color-blue-2;
        }
        @include mobile {
            left: calc(100vw - 14.2rem);
        }
    }
    &__type {
        display: flex;
        padding: 1.5rem 1.6rem 1.6rem; // -1 for border

        &--description {
            display: flex;
            margin-left: 1.6rem;
            max-width: 19.2rem;
            flex-direction: column;

            .cfd-account-card--paragraph {
                width: 18.3rem;
                padding-top: 0.8rem;
            }
        }
        &--has-banner {
            .cfd-account-card--paragraph {
                width: 18rem;
            }
        }
    }
    &__display {
        display: flex;
        flex-wrap: wrap;
        width: min-content;
        justify-content: center;
        height: fit-content;

        &-text {
            @include typeface(--title-center-bold-black);
            color: var(--brand-red-coral);
            line-height: 1.5;
            margin-top: 0.4rem;

            &--top-right {
                position: relative;
                left: 22.2rem;
                bottom: 7.2rem;
                margin: 0;
            }
        }
        &--fixed-height {
            height: 64px; // don't leave space for the demo text that is moved to the top right
        }
