    }
}
.cfd-accounts-compare-modal {
    display: flex;
    flex-flow: column nowrap;
    flex-grow: 1;
    background: inherit;
    height: 100%;
    position: relative;
    &-row-eu {
        grid-template-columns: 0.67fr 1fr;
    }
    @for $column_count from 1 through 7 {
        .cfd-accounts-compare-modal__row-with-columns-count-#{$column_count} {
            grid-template-columns: repeat($column_count, 1fr);
        }
    }
    &__table-header {
        grid-template-columns: 11rem 38rem 46rem;
        &__pre-appstore {
            grid-template-columns: 11rem 36rem 46rem 13.5rem;
        }
    }
    @for $financial_accounts_count from 0 through 6 {
        @for $synthetic_accounts_count from 0 through 2 {
            .cfd-accounts-compare-modal__table-header-for-synthetic-#{$synthetic_accounts_count}-financial-#{$financial_accounts_count} {
                @if $synthetic_accounts_count > 0 and $financial_accounts_count > 0 {
                    grid-template-columns: 1fr ($synthetic_accounts_count + fr) ($financial_accounts_count + fr);
                    &__pre-appstore {
                        grid-template-columns: 1fr ($synthetic_accounts_count + fr) (
                                $financial_accounts_count + fr
                            ) 1fr;
                    }
                } @else if $synthetic_accounts_count == 0 and $financial_accounts_count == 0 {
                    grid-template-columns: 1fr;
                    &__pre-appstore {
                        grid-template-columns: 1fr 1fr;
                    }
                } @else if $synthetic_accounts_count == 0 {
                    grid-template-columns: 1fr ($financial_accounts_count + fr);
                    &__pre-appstore {
                        grid-template-columns: 1fr ($financial_accounts_count + fr) 1fr;
                    }
                } @else {
                    grid-template-columns: 1fr ($synthetic_accounts_count + fr);
                    &__pre-appstore {
                        grid-template-columns: 1fr ($synthetic_accounts_count + fr) 1fr;
                    }
                }
            }
        }
    }
    &__table-header-item {
        justify-content: center;
    }

    &__table-row {
        grid-template-columns: 11rem 13.4rem 12.6rem 12rem 13.4rem 12.6rem 10rem 10rem;
        &__pre-appstore {
            grid-template-columns: 11rem 13.4rem 12.6rem 10rem 13.4rem 12.6rem 10rem 10rem 13.5rem;
        }
        &--leverage {
            grid-template-columns: 11rem 38rem 36rem 10rem;
            &__pre-appstore {
                grid-template-columns: 11rem 36rem 36rem 10rem 13.5rem;
            }
        }
        &--platform {
            &__pre-appstore {
                grid-template-columns: 11rem 82rem 13.5rem;
            }
        }
        &--instruments {
            grid-template-columns: 11rem 38rem 26rem 10rem 10rem;
            &__pre-appstore {
                grid-template-columns: 11rem 36rem 26rem 10rem 10rem 13.5rem;
            }
        }
    }

    &__table-row-item {
        flex-direction: column;
        justify-content: center;
        &--tooltip {
            flex-direction: row;
        }
    }
    &__table-footer {
        grid-template-columns: 11rem 13.4rem 12.6rem 12rem 13.4rem 12.6rem 10rem 10rem;
        height: 4.8rem;
        &__pre-appstore {
            grid-template-columns: 11rem 13.4rem 12.6rem 10rem 13.4rem 12.6rem 10rem 10rem 13.5rem;
        }
        &__item {
            height: 4.8rem;
            justify-content: center;
            &--verification-pending {
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                padding: 3px 8px;
                background: rgba(255, 68, 79, 0.16);
                opacity: 0.32;
                border-radius: 4px;
                width: 100%;
                cursor: not-allowed;
                .dc-text {
                    font-style: normal;
                    font-weight: 700;
                    color: var(--brand-red-coral);
                }
            }
        }
        &__button {
            width: 100%;
        }
    }

    .dc-table {
        margin: 0 25px;

        &__header {
            border: none;
            height: 4rem;
            top: 0;
            z-index: 10;
        }
        &__cell:not(.cfd-accounts-compare-modal__table-empty-cell),
        &__head:not(.cfd-accounts-compare-modal__table-empty-cell) {
            padding: 0.8rem;
            border: 1px solid var(--general-active);
            margin-left: -1px;
            margin-bottom: -1px;
        }
        &__cell {
            padding: 1rem 0.8rem;
        }
        &__row {
            border-bottom: none;
            min-height: 4rem;
        }

        &--scrollbar {
            display: block; // Safari needs this to work properly!
        }
    }
    &-modal {
        &__wrapper {
            display: flex;
            justify-content: center;
            margin-top: 2.4rem;
        }
    }
    @include mobile {
        height: 100%;
        font-size: 1.2rem;

        .dc-table {
            height: 100%;
            margin: 25px;
            overflow-x: auto;
            &__cell {
                &--fixed {
                    background-color: var(--general-main-1);
                    .dc-text {
                        font-size: var(--text-size-xxxs);
                        text-align: left;
                    }
                }
            }
        }
        &__table {
            &-header,
            &-footer,
            &-row,
            &-row--leverage,
            &-row--instruments {
                width: 250vw;
                &__pre-appstore {
                    width: 250vw;
                }
            }
        }
        .cfd-accounts-compare-modal {
            &__table-empty-cell.dc-table {
                &__cell--fixed {
                    margin-left: -1px;
                    border: 1px solid var(--general-active);
                    border-width: 1px 1px 0px 0px;
                }
                &__head {
                    border-top: none;
                }
            }
            &-row-eu {
                width: unset;
                height: unset;
            }
        }
        @for $column_count from 1 through 7 {
            .cfd-accounts-compare-modal__row-with-columns-count-#{$column_count} {
                width: calc(250vw / 7 * #{$column_count});
                &__pre-appstore {
                    width: calc(250vw / 7 * #{$column_count + 1});
                }
            }
        }
        @for $financial_accounts_count from 0 through 6 {
            @for $synthetic_accounts_count from 0 through 2 {
                .cfd-accounts-compare-modal__table-header-for-synthetic-#{$synthetic_accounts_count}-financial-#{$financial_accounts_count} {
                    width: calc(250vw / 7 * (#{$synthetic_accounts_count} + #{$financial_accounts_count} + 1));
                    &__pre-appstore {
                        width: calc(250vw / 7 * (#{$synthetic_accounts_count} + #{$financial_accounts_count} + 2));
                    }
                }
            }
        }
    }
}
.dc-modal-header__title--cfd-accounts-compare-modal {
    font-size: var(--text-size-sm);
    line-height: 3rem;
}

.cfd-accounts-compare-modal-mobile-header {
    height: fit-content;
    border-bottom: none;
    .dc-text {
        font-size: var(--text-size-s);
    }
}
.cfd-compare-accounts-tooltip {
