        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 8px;
        width: 276px;
        border-radius: 4px;
    }
}

.switch-to-real-account-modal {
    .dc-dialog__content {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}
.da-icon-with-message__button {
    margin: 2rem;
}

/* stylelint-enable */
